body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

p + p {
  margin-top: 0;
}

a {
  color: #b774b7;
  text-decoration: none;
}

.App {
  text-align: center;
  margin: 0 auto;
  width: 100%;
  display: block;
  background-color: #c8edfb;
  min-height: 100vh;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 1vmin);
  color: #131313;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.container {
  display: block;
  width: 56%;
}

@media screen and (max-width: 650px) {
  .container {
    display: block;
    width: 95%;
  }
}

.navbar {
  display: block;
  padding-bottom: 15px;
}

.collapse {
  display: inline-block;
}

.navbar-brand {
  color: #6795a7;
  text-decoration: none;
  font-size: 40px;
  display: inline-block;
}

.navbar-nav {
  display: initial;
}

.navbar-item {
  display: initial;
  padding: 10px;
}

table {
  width: 100%;
  border-collapse: collapse;
}

td {
  position: relative;
  min-height: 35px;
  padding: 5px;
  width: 20%;
}

td > a {
  color: black;
  text-decoration: none;
  border: 1px solid rgb(118, 118, 118);
  background-color: #8cb5c5;
  padding: 2px 12px;
  border-radius: 3px;
  font-size: calc(10px + 1vmin);
  font-family: Arial;
}

button {
  background-color: #8cb5c5;
  cursor: pointer;
  font-size: calc(10px + 1vmin);
}

input[type='submit' i] {
  background-color: #cfe4ec;
  cursor: pointer;
  font-size: calc(10px + 1vmin);
}

th {
  background: #def5ff;
  font-weight: bold;
}
td,
th {
  line-height: 1.5rem;
  vertical-align: middle;
  border: 1px solid #94b6c3;
  text-align: left;
}

@media screen and (max-width: 650px) {
  td,
  th {
    line-height: 1rem;
  }
}

.form-group {
  display: block;
}

.newTask {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 1vmin);
  color: #131313;
}

input[type='text' i] {
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-size: 16px 18px;
  background-position: 98% 50%;
  cursor: pointer;
  box-sizing: border-box;
  width: 100%;
  font-size: calc(10px + 1vmin);
  min-height: 35px;
  background-color: #daf0f9;
}
